#root{
    overflow: hidden;
}

.header{
    padding: 35px;
    background-color: cornflowerblue;
    height: 200px;
}
.bgclr{
    background-color: whitesmoke;
}
.overviewDiv{
    background-color: white;
    height: 450px;
    border-radius: 100px;
    margin-top: -116px;
    margin-bottom: 14px;
    /* box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5); */
   box-shadow:  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
.select{
    width: 275px;
    padding: 9px 30px;
    border-radius: 20px
}
.verticalLine{
    border-left: 1px solid darkgrey;
    height: 78%;
    position: absolute;
    left: 50%;
    top: 27px;
}
p.covidFont{
    font-family: cursive;
    font-variant: proportional-nums;
    font-weight: 600;
    color: white;
}    
.iconDetails{
    width: 50px;
    float: left;
}
.countryStyle{
    margin-left: 100px;
    font-variant: all-petite-caps;
}
.worldStyle{
    font-variant: all-petite-caps;
    padding-top: 30px;
}
.casesFont{
    font-family: none;
    font-weight: 600;
}

#upwards{
   font: initial;
    float: left;
}
.confirmedDigit{
    color: salmon;
}
.recoveredDigit{
    color: teal;
}
#digitSize{
    font-size: smaller;
}
.tableheading{
    background: blanchedalmond;
    margin-top: 5%;
    margin-left: 10%;
    margin-right: 10%;
    border-radius: 10px;
}
#tabletext{
    padding-left: 35px;
    font-variant: small-caps;
    font-size: larger;
    font-weight: 800;
}
.footer{
    background: cornflowerblue;
}
.tabledata{
    margin-left: 10%;
    margin-right: 10%;
    border-radius: 10px;
}
.tabledata:hover {
    box-shadow: 0 0 11px rgba(33,33,33,.2); 
  }
